<template>
  <div id="music" class="music">
    <!-- 视频播放 -->
      <div class="video_con" v-show="isShow" style="display: none;">
        <video :src="mvUrl" controls="controls"></video>
        <!-- <div class="mask" @click="hide">点击隐藏</div> -->
      </div>
      <div class="mask" @click="hide">点击隐藏</div>
    <div class="main">      
      <!-- 上中下布局 -->
      <div class="top">
        <!-- 嵌入一个div头 -->
        <div class="head">
          <div class="search_bar">
            <!-- 搜索歌曲 -->
            <input
              type="text"
              autocomplete="off"
              placeholder="搜索歌曲..."
              v-model="query"
              @keyup.enter="searchMusic"
            />
          </div>
        </div>
      </div>
      <div class="content">
        <!-- 中间分两列 -->
        <div class="left">
          <div class="song_wrapper test-1">
            <ul class="song_list">
              <li v-for="(item,index) in musicList" :key="index">
                <a href="javescript:;" @click="playMusic(item.id)">
                  <b>{{ item.name }}-</b>
                  <i class="h3">{{ item.artists[0].name }} </i>
                </a>
                <span v-if="item.mvid != 0" @click="playMv(item.mvid)">
                  <!-- <Icon type="step-backward" /> -->
                  <!-- <Icon type="skip-forward"></Icon> -->
                  <i class="mv">MV</i>
                </span>
              </li>
            </ul>
          </div>
        </div>
        <div class="right">
          <!-- 中间右边再分两列 -->
          <div class="sub_l">
            <!-- 歌曲信息容器 -->
            <div class="player_con" :class="{playing: isPlaying}">
              <!-- 黑胶唱片 -->
              <img src="../assets/唱片.png" class="disc autoRotate" />
              <img :src="musicCover" class="cover autoRatate" />
            </div>
          </div>
          <!-- 显示歌词 -->
          <div class="sub_r">
            <div class="lyric test-1" v-html="lyricText"></div>
          </div>
        </div>
      </div>
      <div class="foot">
        <div class="audio_con">
          <audio
            ref="auto"
            @play="play"
            @pause="pause"
            :src="musicUrl"
            controls
            autoplay
            loop
            class="myaudio"
          ></audio>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { reactive, toRefs } from "@vue/composition-api";
export default {
  name: "music",
  // Vue3.0的核心方法
  // 回顾：setup
  // 1.自动执行->在created方法调用之前执行
  // 2.作用->为当前组件提供数据或者方法
  // 3.合并到组件的data中
  // 4.形参props context
  // 5.return {数据的名字：值}
  // setup(props,context){
  //   return {}
  // }
  setup() {
    //data
    const state = reactive({
      query: "",
      musicList: [],
      musicUrl: "",
      musicCover: "",
      isPlaying: false,
      isShow: false,
      mvUrl: "",
      lyric: "",
      lyricText: ""
    }); //state->{query:''}

    //方法
    //搜索音乐
    const searchMusic = async () => {
      //
      const { data } = await axios.get(
        `https://autumnfish.cn/search?keywords=${state.query}`
      );
      state.musicList = data.result.songs;
    };
    //播放音乐
    const playMusic = async musicId => {
      //音乐播放
      const { data } = await axios.get(
        `https://autumnfish.cn/song/url?id=` + musicId
      );
      state.musicUrl = data.data[0].url;      
      //封面展示
      const { data: datadetail } = await axios.get(
        `https://autumnfish.cn/song/detail?ids=` + musicId
      );
      state.musicCover = datadetail.songs[0].al.picUrl;
      //评论
      //搜索歌词
      const { data: datalyric } = await axios.get(
        `https://autumnfish.cn/lyric?id=` + musicId
      );
      state.lyricText = ""; 
      state.lyric = datalyric.lrc.lyric;
      //用v-html标签代替{{}} '\n'替换为'<br>'         
      state.lyricText = state.lyric.replace(/\n/g, '<br>')
    };
    //播放和暂停
    const play = () => {
      state.isPlaying = true;
    };
    const pause = () => {
      state.isPlaying = false;
    };
    //播放mv
    const playMv = async mvid => {
      const { data } = await axios.get(
        `https://autumnfish.cn/mv/url?id=` + mvid
      );
      state.isShow = true;
      state.mvUrl = data.data.url;
    };
    //蒙层
    const hide = () => {
      state.isShow = false;
    };
    //包装多个方法
    const methods = {
      searchMusic,
      playMusic,
      play,
      pause,
      playMv,
      hide
    };

    return {
      //data->{query:''}
      //query:state/query
      //
      //ES6展开...是一个全拷贝数据
      // ...state
      //转换数据toRefs()
      ...toRefs(state),
      ...methods
    };
  }
};
</script>

<style>
@import "../css/index.css";
</style>